import Skill from './Skill.js'

export default class Dodge extends Skill {
  constructor() {
    super({
      id: 'skill-dodge',
      name: 'Dodge',
      description: 'Brief damage immunity',
      cost: 20,
      targetMode: 'self'
    });
  }
  skillAction(target) {
    target.addStatusEffect({
      name: 'Dodge',
      description: 'Brief damage immunity',
      duration: 1000,
      effect: {
        stat: 'dodge',
        value: 100,
        type: 'set'
      }
    })
    return;
  }
}