import Character from './Character.js'

export default class Mob extends Character {
  constructor(name, level, target) {
    super(name, level);
    this.target = target;
  }
  ai() {
    // use basic attack if we have enough energy
    if (typeof this.target === "undefined") return false;
    if (this.energy >= this.skills[0].cost) {  
      this.useSkill('skill-basic-attack', this.target);
    }
  }
}