<template>
  <div v-bind:class="{ targeted: characterData.targeted }" class="character" @click="target">
    <h1>{{ characterData.name }}</h1>
    <ul>
      <li>Level: {{ characterData.level }}</li>
      <li>Energy: {{ characterData.energy.toFixed(0) }} / {{ characterData.derivedStats.maxEnergy }}</li>
      <li>Health: {{ characterData.health }} / {{ characterData.derivedStats.maxHealth }}</li>
      <li>Targeted: {{ characterData.targeted }}</li>
      <li>{{ characterData.derivedStats }}</li>
    </ul>
    <h3>Skills</h3>
    <!-- <ul style="display: flex; list-style: none;">
      <li v-for="skill in characterData.skills" :key="skill.id"><Skill :skillData="skill" :player="player" /></li>
    </ul> -->
    <ul class="skills">
      <li v-for="skill in characterData.skills" :key="skill.id">
        <button
          :disabled="skill.onCooldown || characterData.energy < skill.cost"
          type="button"
          @click="useSkill(skill.id)"
        >
          {{ skill.name }}<br />
          {{ skill.description }}<br />
          cost: {{ skill.cost }}<br />
          cooldown: {{ skill.currentCooldown }}<br />
          onCooldown: {{ skill.onCooldown }}<br />
        </button>
      </li>
    </ul>
    <h3>Status effects</h3>
    <ul>
      <li v-for="effect in characterData.statusEffects" :key="effect.name">
        {{ effect.name }}<br />
        {{ effect.description }}<br />
        {{ effect.duration }}
      </li>
    </ul>
  </div>
</template>

<script>
// import Skill from "./Skill.vue";

export default {
  components: {
    // Skill
  },
  props: {
    characterData: Object,
    player: Boolean
  },
  methods: {
    useSkill(id) {
      this.$emit("skill", id);
    },
    target() {
      this.$emit("target", this);
    }
  },
  mounted() {
    if (this.player) {
      window.addEventListener("keyup", event => {
        switch (event.key) {
          case "1":
            this.useSkill(this.characterData.skills[0].id);
            break;
          case "2":
            this.useSkill(this.characterData.skills[1].id);
            break;
          case "3":
            this.useSkill(this.characterData.skills[2].id);
            break;
          default:
            break;
        }
      });
    }
  }
};
</script>

<style scoped>
ul {
  list-style-type: none;
}
.skills {
  display: flex;
}
.skills li {
  flex: 1 0 100px;
}
.skills button {
  padding: 2%;
}
.character {
  border: 1px solid navy;
  border-radius: 0.2rem;
  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.3);
  padding: 5%;
  user-select: none;
  transition: all 0.1s ease-out;
  margin-bottom: 5%;
}
.targeted {
  transform: scale(1.02);
  border: 3px solid red;
  box-shadow: 3px 3px 8px rgba(0, 0, 0, 0.5);
}
</style>
