import Skill from './Skill.js'

export default class BasicAttack extends Skill {
  constructor() {
    super({
      id: 'skill-basic-attack',
      name: 'Basic attack',
      description: 'A basic attack, nothing special',
      cost: 30
    })
    this.basePower = 10;
  }
  skillAction(target, user) {
    target.handleHit({
      power: this.basePower * user.derivedStats.level
    })
  }
}