<template>
  <div class="combat">
    <div class="debug">
      <div>
        <h3>Create player</h3>
        <br />
        Name: <input type="text" name="player-name" v-model="playerName"><br />
        Level: <input type="number" name="player-level" v-model="playerLevel"><br />
        <input type="button" value="Create player" @click="newPlayer">
      </div>
      <div>
        <h3>Create mob</h3>
        Level: <input type="number" name="mob-count" v-model="mobLevel"><br />
      <input type="button" value="Create mob" @click="newMob">
      </div>
      <div>
        <h3>Statistics</h3>
        {{ (1000 / performance.dt).toFixed(0) }} FPS
      </div>
    </div>
    <section>
      <Character
      v-if="playerOne"
      :characterData="playerOne"
      v-on:skill="useSkill"
      :player="true"
    />
    </section>
    <section>
      <Character v-for="mob in mobs" :key="mob.id" :characterData="mob" v-on:target="setTarget" />
    </section>
  </div>
</template>

<script>
// classes
import Player from "../Classes/Player.js";
import Mob from "../Classes/Mob.js";

// components
import Character from "./Character.vue";

export default {
  components: {
    Character
  },
  data() {
    return {
      playerName: '',
      mobLevel: 1,
      playerLevel: 1,
      playerOne: undefined,
      mob: undefined,
      mobs: [],
      target: undefined,
      performance: {
        dt: 0,
        last: 0
      }
    };
  },
  methods: {
    newPlayer() {
      this.playerOne = new Player(this.playerName, this.playerLevel);
      // set target for any mobs to player
      this.mobs.forEach(mob => {
        mob.target = this.playerOne;
      })
    },
    newMob() {
      this.mobs.push(new Mob("mob", this.mobLevel, this.playerOne));
    },
    setTarget(target) {
      this.mobs.forEach(mob => {
        if (mob === target.characterData) {
          mob.targeted = ! mob.targeted;
          if (mob.targeted) {
            this.target = mob;
          }
        } else {
          mob.targeted = false;
        }
      })
    },
    playerDeath() {
      // delete player and remove targeting from mobs
      console.log('Game over, create a new player to try again');
      this.mobs = [];
      this.playerOne = undefined;
      this.mobs.forEach(mob => {
        mob.target = undefined;
      })
    },
    mobDeath(mob) {
      // cleanup dead mobs
      console.log('dead mob', mob);
      this.mobs = this.mobs.filter(currentMob => {
        return currentMob.health > 0;
      })
    },
    useSkill(id) {
      this.playerOne.useSkill(id, this.target);
    },
    update(dt) {
      // update player
      if (typeof this.playerOne !== 'undefined') {
        this.playerOne.update(dt);

        if (this.playerOne.health <= 0) {
          this.playerDeath();
        }
      }

      // update mobs
      this.mobs.forEach(mob => {
        mob.update(dt);

        if (mob.health <= 0) {
          this.mobDeath(mob);
        }
      })
    },
    mainLoop() {
      window.requestAnimationFrame(this.mainLoop);
      var now = performance.now();
      this.performance.dt = now - this.performance.last;

      this.update(this.performance.dt);

      this.performance.last = now;
    }
  },
  mounted() {
    // start main loop
    this.mainLoop();
  }
};
</script>

