  background: linear-gradient(#262626, #131313);
  box-shadow: 0 0 20px rgba(0, 0, 0, 0.6);
  flex: 100%;
  color: white;
  display: flex;
  padding: 2%;
}
.debug div {
  margin-right: 2%;
}
section {
  flex: 1 0 100px;
  padding: 5%;
}
