import BasicAttack from './BasicAttack.js';
import Dodge from './Dodge.js';
import Heal from './Heal.js'

import { v4 as uuidv4 } from 'uuid';

export default class Character {
  constructor(name, level) {
    this.id = uuidv4();
    this.level = level;
    this.name = name;
    this.skills = [];
    this.statusEffects = [];
    this.skills.push(new BasicAttack());
    this.skills.push(new Dodge());
    this.skills.push(new Heal());

    // move these to dynamic values later
    this.energy = 0;
    // this.energyMax = 100;
    this.energyRegenModifier = 0.01;

    // this.healthMax = 100;
    this.health = this.derivedStats.maxHealth;
  }
  handleHit(hit) {
    if (this.derivedStats.dodge / 100 > Math.random()) {
      console.log('dodged attack');
      return;
    }
    this.health -= hit.power;
  }
  handleHeal(heal) {
    this.health += heal;

    if (this.health > this.derivedStats.maxHealth) {
      this.health = this.derivedStats.maxHealth;
    }
  }
  addStatusEffect(effect) {
    // if status exists, refresh duration
    var existingEffect = this.statusEffects.filter(e => {
      return effect.name === e.name;
    })

    if (existingEffect.length > 0) {
      console.log('effect already exists, refresh duration');
      existingEffect = effect;
    } else {
      this.statusEffects.push(effect);
      console.log('added status effect', this.statusEffects);
    }
  }
  energyRegen(dt) {
    // regen energy
    this.energy += (dt * this.energyRegenModifier);
    if (this.energy > this.derivedStats.maxEnergy) {
      this.energy = this.derivedStats.maxEnergy;
    }
  }
  getDerivedStat(stat) {
    var statValue = 0;
    this.statusEffects.forEach(statusEffect => {
      if (statusEffect.effect.stat === stat) {
        switch (statusEffect.effect.type) {
          case "set":
            // set stat to given value
            statValue = statusEffect.effect.value;
            break;
        
          default:
            break;
        }
      }
    })
    return statValue;
  }
  statusEffectUpdate(dt) {
    // tick status effects and remove any that are expired
    this.statusEffects.forEach(statusEffect => {
      // if (statusEffect.effect.type === "set") {
      //   this[statusEffect.effect.stat] = statusEffect.effect.value;
      // }
      statusEffect.duration -= dt * 1;
      if (statusEffect.duration <= 0) {
        this.statusEffects = this.statusEffects.filter(eff => {
          return eff !== statusEffect;
        })
      }
    })
  }
  ai() {
    return;
  }
  update(dt) {
    this.energyRegen(dt);
    this.statusEffectUpdate(dt);

    // ai fuction, for mobs
    this.ai();

    // skill updates
    this.skills.forEach(skill => {
      skill.update(dt);
    })
  }
  useSkill(id, target) {
    var skill = this.skills.filter(skill => {return skill.id === id})[0];
    if (typeof skill !== 'object') {
      console.log('no skill with id ' + id + ' found');
      return false;
    }

    // console.log(skill.targetMode);
    if (skill.targetMode === 'self') {
      target = this;
    }
    if (typeof target !== 'object') {
      console.log('invalid target: ' + target);
      return false;
    }
    if (this.energy < skill.cost) {
      console.log('Not enough energy to use skill: ' + skill.name);
      return false;
    }
 
    if (skill.use(target, this)) {
      this.energy -= skill.cost;
      return true;
    }
  }
  get derivedStats() {
    return {
      level: this.level,
      // temporary level scaling
      maxEnergy: (this.level * 10) + 90,
      maxHealth: this.level * 100,
      dodge: this.getDerivedStat("dodge")
    }
  }
}