import Skill from './Skill.js';

export default class Heal extends Skill {
  constructor() {
    super({
      id: "skill-heal",
      name: "Heal",
      description: "Heal a small amount of HP",
      cost: 50,
      targetMode: "self"
    })
    this.healAmount = 30;
  }
  skillAction(target) {
    target.handleHeal(this.healAmount);
  }
}