export default class Skill {
  constructor(options) {
    // error handling, id is the only required field
    if (typeof options.id !== "string") {
      console.log('[skill contructor] invalid id: ' + options.id + ' Please provide a valid string');
      return false;
    }

    this.id = options.id;
    this.name = options.name || 'Unnamed skill';
    this.description = options.description || 'Skill description';
    this.cost = options.cost || 10;
    this.cooldown = options.cooldown || 50;
    // targeting mode, valid options: 'target' or 'self'
    this.targetMode = options.targetMode || 'target';
    this.currentCooldown = 0;
  }
  update(dt) {
    this.currentCooldown -= dt;
      if (this.currentCooldown < 0) {
        this.currentCooldown = 0;
      }
  }
  // skill logic
  skillAction(target, user) {
    return target, user;
  }
  // cooldown and error handling wrapper
  use(target, user) {
    if (typeof target !== 'object') {
      console.log('invalid target: '+ target);
      return false;
    }
    if (this.onCooldown) {
      console.log('Skill ' + this.name + ' is on cooldown');
      return false;
    } 

    // console.log('using skill: ' + this.name);
    this.skillAction(target, user);
    this.currentCooldown = this.cooldown;
    return true;
  }
  // is skill on cooldown?
  get onCooldown() {
    if (this.currentCooldown > 0) {
      return true;
    } else {
      return false;
    }
  }
}